import { Popconfirm, Button } from "antd";
import React, { useState } from "react";

let ReturnStepBackConfirm = React.memo(({ reasonText, dataProps, setState, user, ...props }) => {

    const [confirmText, setConfirmText] = useState('         ?');

    const confirm = () => {
        if (reasonText != null || reasonText?.length > 0) {
            setState(prevState => {
                let old = Object.assign({}, prevState);
                old.reason = null;
                old.reason = dataProps.initialValues2.documents[0].reason ? dataProps.initialValues2.documents[0].reason : [];
                old.reason.push({ text: ': ' + reasonText, userId: user.id, userFio: user.fio, userPosition: user.position_names[0], userName: user.username });
                return old;
            });
        };
        dataProps.handleRouteBackward2()
        dataProps?.form2?.submit()
    };

    return (
        <Popconfirm
            title={confirmText}
            placement="topLeft"
            disabled={dataProps.disabled}
            onConfirm={confirm}
            okText=""
            cancelText="">
            <Button
                disabled={dataProps.disabled}
                type='primary'
                onClick={() => {
                    if (reasonText == null || reasonText.length == 0) {
                        setConfirmText('        ?')
                    }
                    else {
                        setConfirmText('         ?');
                    }
                }}>
                 
            </Button>
        </Popconfirm>
    )
});

export default ReturnStepBackConfirm;