import {Button, Result} from "antd";
import {NavLink} from "react-router-dom";
import React from "react";

const Error404 = () => {
    return (
        <Result
            status="404"
            title="404"
            subTitle=',      .'
            extra={<Button type="primary"><NavLink to='/'> </NavLink></Button>}
        />
    )
}

export default Error404;